.wifi-icon {
  width: 20px;
  height: 20px;
  display: inline-block;
  position: relative;
  overflow: hidden;
  margin: 0 auto;
  float: left
}

.wifi-icon > span {
  display: block;
  position: absolute;
  border-radius: 50%;
}

.wifi-icon .wifi-1 {
  width: 5px;
  height: 5px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  bottom: 1px;
  background: currentcolor;
}

.wifi-icon .wifi-2, .wifi-icon .wifi-3, .wifi-icon .wifi-4 {
  border: 2px solid;
  border-color: currentcolor transparent transparent;
}

.wifi-icon .wifi-2 {
  width: 12px;
  height: 12px;
  left: 4px;
  bottom: -2px;
}

.wifi-icon .wifi-3 {
  width: 20px;
  height: 20px;
  left: 0px;
  bottom: -6px;
}

.wifi-icon .wifi-4 {
  width: 28px;
  height: 28px;
  left: -4px;
  bottom: -10px;
}

.wifi-icon > span:after {
  content: "";
  display: block;
  position: absolute;
}

.wifi-icon .lock {
  width: 6px;
  height: 5px;
  background: currentcolor;
  display: block;
  right: 0;
  bottom: 1px;
  border-radius: 0;
}

.wifi-icon .lock:after {
  width: 4px;
  height: 3px;
  border: 1px solid;
  border-radius: 100px 100px 0 0;
  border-bottom: none;
  left: 1px;
  bottom: 100%;
}

.wifi-icon .key {
  width: 5px;
  height: 5px;
  -webkit-box-shadow: 0 0 0 1px inset;
          box-shadow: 0 0 0 1px inset;
  display: block;
  right: 0;
  bottom: 1px;
}

.wifi-icon .key:after {
  width: 1px;
  height: 1px;
  background: currentcolor;
  -webkit-box-shadow: 0 -1px, 0 -2px, 0 -3px, 0 -4px, 1px -3px, 2px -3px;
          box-shadow: 0 -1px, 0 -2px, 0 -3px, 0 -4px, 1px -3px, 2px -3px;
  bottom: 4px;
  left: 2px;
}

